import React, {Component} from 'react';
import Computer from './images/image-computer.png';
import Devices from './images/image-devices.png';
import BlackList from './images/icon-blacklist.svg';
import IconText from './images/icon-text.svg';
import IconPreview from './images/icon-preview.svg';
import LogoGoogle from './images/logo-google.png';
import LogoHp from './images/logo-hp.png';
import LogoMicrosoft from './images/logo-microsoft.png';
import LogoIbm from './images/logo-ibm.png';
import Vector from './images/logo-vector-graphics.png';


export default class MainContent extends Component {
    render() {
        return (
            <div>
                <section className="container">
                    <div className="container container-small">

                    <h2>Keep track of your snippets</h2>

                    <p>Clipboard instantly stores any item you copy in the cloud, 
                        meaning you can access your snippets immediately on all your 
                        devices. Our Mac and iOS apps will help you organize everything.
                    </p>   

                    </div>

                    <div className="flex">

                        <div>
                            <img src={Computer} alt="computer"/>
                        </div>

                        <div className="right-section">

                            <h3>Quick Search</h3>
                            <p>Easily search your snippets by content, category, web address, application, and more.</p>

                            <h3>iCloud Sync</h3>
                            <p>Instantly saves and syncs snippets across all your devices.</p>

                            <h3>Complete History</h3>
                            <p>Retrieve any snippets from the first moment you started using the app.</p>

                        </div>
                    </div>  
                </section>

                <section className="container container-small">  

                    <h2>Access Clipboard anywhere</h2>

                    <p>Whether you’re on the go, or at your computer, you can access all your Clipboard 
                    snippets in a few simple clicks.
                    </p>   
                    <img src={Devices} alt="devices"/>

                </section>

                <section className="container">

                    <h2>Supercharge your workflow</h2>

                    <p>We’ve got the tools to boost your productivity.</p>

                    <div className="flex">
                        <div>
                            <img src={BlackList} alt="icon-balck-list"/>
                            <h3>Create blacklists</h3>
                            <p>Ensure sensitive information never makes its way to your clipboard by excluding certain sources.</p>
                        </div>

                        <div>
                            <img src={IconText} alt="icon-text"/>
                            <h3>Plain text snippets</h3>
                            <p>Remove unwanted formatting from copied text for a consistent look.</p>
                        </div>  

                        <div>
                            <img src={IconPreview} alt="icon-preview"/>
                            <h3>Sneak preview</h3>
                            <p>Quick preview of all snippets on your Clipboard for easy access.</p>
                        </div>
                    </div>
                </section>

                <div className="container flex">
                    <div>
                        <img src={LogoGoogle} alt="logo-google"/>
                    </div>

                    <div>
                        <img src={LogoHp} alt="logo-hp"/>
                    </div>

                    <div>
                        <img src={LogoMicrosoft} alt="logo-microsoft"/>
                    </div>

                    <div>
                        <img src={LogoIbm} alt="logo-ibm"/>
                    </div>

                    <div>
                        <img src={Vector} alt="logo-vector-graphics"/>
                    </div>  
                </div>  

                <div className="container container-small m4">
                    <h2>Clipboard for iOS and Mac OS</h2>

                    <p>Available for free on the App Store. Download for Mac or iOS, sync with iCloud 
                    and you’re ready to start adding to your clipboard.
                    </p>   

                    <div>
                    <button className="btn btn-primary">Download for iOS</button>
                    <button className="btn btn-secondary">Download for Mac</button>
                    </div>
                </div>
            </div>
        )
    }    
}

